header {
  width: 100%;
  position: relative;
}
header nav {
  width: 100%;
  padding: 20px 0px;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 11;
}
header nav a.logo {
  font-size: 26px;
  font-weight: 700;
  color: white;
  cursor: pointer;
}
header nav a.logo span {
  color: crimson;
}
header nav .bars {
  display: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  header nav .bars {
    display: flex;
    gap: 10px;
    align-items: center;
  }
}
header nav .bars .iconDark-menu-sm {
  border: none;
  cursor: pointer;
  color: white;
}
header nav .bars button {
  background-color: transparent;
  border: none;
  font-size: 18px;
  border: 2px solid white;
  padding: 5px 10px;
  border-radius: 6px;
  display: block;
  cursor: pointer;
  color: white;
}
header nav .navbar-menu {
  display: flex;
  gap: 30px;
  align-items: center;
}
@media (max-width: 767px) {
  header nav .navbar-menu {
    display: none;
    margin-top: 20px;
  }
}
header nav .navbar-menu .iconDark-menu {
  cursor: pointer;
  color: white;
}
@media (max-width: 767px) {
  header nav .navbar-menu .iconDark-menu {
    display: none;
  }
}
@media (max-width: 767px) {
  header nav .navbar-menu li {
    width: 100%;
  }
}
@media (max-width: 767px) {
  header nav .navbar-menu li {
    margin-bottom: 10px;
  }
}
header nav .navbar-menu li a {
  font-size: 14px;
  font-weight: 500;
  line-height: 24px;
  color: white;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  transition: 0.5s;
}
@media (max-width: 767px) {
  header nav .navbar-menu li a {
    display: block;
    border-bottom: 2px solid white;
    padding-bottom: 5px;
    width: 100%;
  }
}
header nav .navbar-menu li a:hover {
  color: crimson;
}
header .header-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
header .header-text h1 {
  font-size: 50px;
  line-height: 50px;
  color: white;
  margin-bottom: 20px;
  font-weight: 900;
}
header .header-text h1 span.textCrimson {
  color: crimson;
}
header .header-text button.bgDark {
  padding: 10px 30px;
  font-size: 20px;
  border-radius: 10px;
  background-color: crimson;
  cursor: pointer;
}
header .carousel {
  width: 100%;
  position: relative;
  height: 100vh;
  overflow: hidden;
}
header .carousel::after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  background-color: rgba(0, 0, 0, 0.5);
}
header .carousel .carousel-img {
  height: 100vh;
  min-width: 100%;
  display: flex;
}
header .carousel .carousel-img img {
  min-width: 100%;
  object-fit: fill;
  height: 100%;
}
header .carousel .indicators {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
}
header .carousel .indicators li {
  width: 40px;
  height: 5px;
  cursor: pointer;
  background-color: white;
}
header .carousel .indicators .activeIndicator {
  background-color: crimson;
  cursor: pointer;
  transition: 0.5s;
}
header .carousel .carousel-transition {
  transition: transform 1s ease-in-out;
}
main {
  width: 100%;
  margin-top: 150px;
  text-align: center;
}
@media (max-width: 767px) {
  main {
    margin-top: 50px;
  }
}
@media (max-width: 991px) {
  main {
    margin-top: 100px;
  }
}
main .box .images {
  width: 150px;
  height: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: 2px solid blue;
  margin: auto;
  margin-bottom: 30px;
}
main .box h3 {
  font-size: 24px;
  font-weight: 700;
  line-height: 29px;
  color: #151515;
  margin-bottom: 30px;
}
main .box p {
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  color: #151515;
}
body {
  font-family: "Inter", sans-serif;
  height: 10000px;
}
button.bgDark {
  padding: 23px 112px;
  color: white;
  font-size: 36px;
  font-weight: 700;
  line-height: 44px;
  border: none;
  background: #151515;
}
img,
video,
svg {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}
.preLoader {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1111;
  flex-direction: column;
  transition: 0.5s;
}
.preLoader h2 {
  color: white;
  font-size: 28px;
  font-weight: 700;
  margin-top: 20px;
}
@media (max-width: 767px) {
  .preLoader h2 {
    font-size: 20px;
  }
}
.preLoader-active {
  opacity: 0;
  transition: 0.5s;
}
.activeNav {
  background: black;
  transition: 0.5s;
  box-shadow: 2px 2px 2px black;
}
.activeNav .activeLink {
  color: white;
}
.modalRegister {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(10px);
  z-index: 111;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.modalRegister .close {
  color: white;
  font-size: 50px;
  background-color: transparent;
  border: none;
  position: absolute;
  top: 50px;
  right: 50px;
  cursor: pointer;
}
.modalRegister .modal-register-body {
  text-align: center;
}
.modalRegister .modal-register-body h2.title {
  font-size: 50px;
  line-height: 50px;
  font-weight: 700;
  margin-bottom: 20px;
  color: white;
}
.modalRegister .modal-register-body form {
  width: 500px;
}
.modalRegister .modal-register-body form input {
  width: 100%;
  padding: 20px 30px;
  border: 2px solid gray;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 500;
  color: white;
  background-color: transparent;
  margin-bottom: 30px;
}
.modalRegister .modal-register-body form button {
  width: 100%;
  padding: 20px 30px;
  border: none;
  background-color: crimson;
  color: white;
  font-size: 14px;
  font-weight: 500;
  border-radius: 10px;
}
.activeModal {
  display: flex;
}
h2.title {
  font-size: 48px;
  font-weight: 700;
  line-height: 58px;
  color: #151515;
  text-align: center;
  margin-bottom: 50px;
}
@media (max-width: 767px) {
  h2.title {
    font-size: 30px;
  }
}
@media (max-width: 991px) {
  h2.title {
    font-size: 35px;
  }
}
p.error {
  width: 100%;
  padding: 10px 20px;
  display: none;
  gap: 20px;
  color: white;
  background-color: crimson;
  margin-top: -10px;
  margin-bottom: 30px;
  border-radius: 10px;
  text-align: center;
  justify-content: center;
}
